import React,{ useState} from "react";
import axios from "axios";


function Userform() {

  
  const [values, setvalues] = useState({
    name:"",
    dob:"11/02/1998",
    email:"",
    mobile:"",
  });



  const handleChange = e =>{
    const {name , value} = e.target
    setvalues({...values,[name]:value})
  }

  const handleSubmit = e =>{
    axios.post("http://localhost:5000/userform", values).then(response => console.log(response.data.email))

  }

  


  return (
    <div className="App" >
      <h1>User Form</h1>
      <form onSubmit={handleSubmit} >
         <input
          type="text"
          name="name"
          placeholder="Name" 
          value={values.name} 
          onChange={handleChange} />
        
         <input 
         type="email" 
         name="email" 
         placeholder="E-Mail" 
         value={values.email} 
         onChange={handleChange} />
        
         <input 
         type="date"
         value={values.dob}
         onChange={handleChange}
          />
         <input  
         name="mobile" 
         placeholder="Mobile No" 
         value={values.mobile} 
         onChange={handleChange} />
         <input
          type="submit" 
          value="Submit" />
      </form>
      

    </div>
  );
}

export default Userform;
